.div header {
  width: 100%;
  margin: 0 auto;
  font-size: 1.15rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding: 0 120px;
  box-shadow: 0px -2px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 300ms ease-in-out;
  z-index: 999;
  color: #73777b;
  color: #668a98;
}

.div header.scrolled {
  /* width: 100vw; */
  display: flex;
  background: #8eaeba;
  box-shadow: 0px -2px 6px rgba(0, 0, 0, 0.1);
  color: #fff;
}

.menu {
  display: flex;
  gap: 2.88rem;
  align-items: center;
  list-style: none;
}
.menu ul {
  margin: 0;
}
.menu li {
  font-size: 1.1rem;
  text-decoration: none;
  font-family: "Playfair Display";
  font-weight: 500;
  border-bottom: none;
  transition: color 300ms ease-in-out;
  cursor: pointer;
  text-transform: uppercase;
  line-height: 70px;
}

.menu a.active {
  font-weight: bold;
  border-bottom: 2px solid #2a2d46;
}

.menu a:hover {
  color: #064db8;
}

.contact {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.contact a {
  color: #000000;
  font-weight: 400;
  line-height: 20px;
}
.lastContainer {
  display: flex;
  gap: 30px;
}
.btns {
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.btns button {
  background: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
.logo {
  filter: invert(100%);
  -webkit-filter: invert(100%);
  width: 50px;
  height: 50px;
}
@media (max-width: 768px) {
  .div header {
    /* width: 100%; */
    padding: 0 16px;
    justify-content: space-between;
    transition: height 300ms ease-in-out;
  }
  .contact,
  .btn {
    display: none;
  }
  .burgerBtn {
    display: block;
    cursor: pointer;
    z-index: 1000;
  }
  .menu {
    display: none;
  }
  .active {
    left: 0;
    display: none;
  }

  .burger {
    display: block;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 26px;
    height: 19px;
    margin-top: 31px;
    position: relative;
    z-index: 10000;
  }
  .burger span {
    display: block;
    height: 3px;
    width: 100%;
    margin-top: 5px;
    -webkit-transition: 0.5s all;
    transition: 0.5s all;
    background-color: #668a98;
    border-radius: 3px;
  }
  .div header.scrolled .burger .shortLine {
    background-color: #fff;
  }
  .div header.scrolled .burger span {
    background-color: #fff;
  }
  .burger .shortLine {
    display: block;
    height: 3px;
    width: 21px;
    -webkit-transition: 0.5s all;
    transition: 0.5s all;
    background-color: #668a98;
    border-radius: 3px;
    margin-left: 5px;
  }

  .burger.active span {
    margin-top: -5px;
  }
  .burger.active span:nth-child(1) {
    -webkit-transform: translateY(3px) rotate(-45deg);
    transform: translateY(3px) rotate(-45deg);
    position: relative;
  }
  .burger.active span:nth-child(2) {
    display: none;
  }
  .burger.active span:nth-child(3) {
    -webkit-transform: translateY(5px) rotate(45deg);
    transform: translateY(5px) rotate(45deg);
  }
}
